<template>
  <div style="padding:20px;">
    <div style="margin-bottom:15px;display: flex;justify-content:flex-end;">
        <!--<div class="select-wrapper">
          <Select v-model="page.state" placeholder='请选择状态' style="width: 100px">
              <Option v-for="item in stateList" :value="item.key" :key="item.key">{{ item.value }}</Option>
          </Select>
        </div>-->
        <div class="select-wrapper">
          <Input v-model="page.keywords" placeholder="请输入手机号"></Input>
        </div>
        <div class="select-wrapper">
          <Button type="ghost" class='fl-btn' @click='getData'>查询</Button>
        </div>
    </div>
    <Table :loading="loading" border :columns="columns4" :data="applyData"></Table>
    <div style="margin-top: 15px;overflow: hidden">
      <div style="float: left;"></div>
      <div style="float: right;">
        <Page :total="totalcount" :page-size="page.pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer
          placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
      </div>
    </div>
    <Modal v-model="showConfirm" fullscreen title="删除确认" @on-ok="delConfirm">
      <div>确定删除该条申请记录吗?</div>
    </Modal>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        loading: false,
        page:{
          pageindex: 0,
          pagesize: 10,
          keywords:'',
          state:-1
        },
        stateList:[
          {
            key:-1,
            value:'全部'
          },{
            key:0,
            value:'未处理'
          },{
            key:1,
            value:'已处理'
          },{
            key:2,
            value:'拒绝'
          }
        ],
        totalcount: 0,
        columns4: [
          {
            title: '#',
            width: 60,
            align: 'center',
            render: (h, params) => {
              return params.index + this.page.pageindex * this.page.pagesize + 1;
            }
          },
          {
            title: '申请人姓名',
            key: 'realname',
            align: 'center',

          },
          {
            title: '申请人电话',
            key: 'telphone',
            align: 'center',
          },
          {
            title: '申请区域',
            key: 'city',
            align: 'center',
          },
          {
            title: '申请日期',
            key: 'createtime',
            width: 180,
            align: 'center',
          },
          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px',
                    width:'80px'
                  },
                  on: {
                    click: () => {
                      this.applyForm.applyid=params.row.id;
                      this.showConfirm=true;
                        /*this.$Message.warning('暂未开放');*/
                    }
                  }
                }, '删除')
              ]);
            }
          }
        ],
        applyData: [],
        applyForm:{
          applyid:''
        },
        showConfirm:false
      }
    },
    mounted: function () {
      this.$sitemap.prvname = "店铺管理";
      this.$sitemap.currname = '店铺申请日志';
      this.$menuactive.open = ['StoreBlock'];
      this.$menuactive.active = 'StoreApply';
      this.$Loading.start();
      this.getData().then(() => {
        this.$Loading.finish();
      });
    },
    methods: {
      //得到用户列表
      getData() {
        this.loading = true;
        return this.$http.post(this.$api.store_apply, this.page).then(res => {
          if (res.data.result == '1' && res.data.data) {
            this.applyData = res.data.data;
            this.totalcount = +res.data.totalcount
          } else {
            this.$Message.error(res.data.msg);
          }
        }).catch(erro => {
          this.$Message.error('服务器异常');
        }).then(() => {
          this.loading = false;
        })
      },
      indexChange(index) {
        this.page.pageindex = index - 1;
        this.getData();
      },
      sizeChange(size) {
        this.page.pagesize = size;
        this.getData();
      },
      delConfirm(){
        this.$http.post(this.$api.storeapply_del, this.applyForm).then(res => {
          if (res.data.result == '1') {
            this.$Message.success('删除成功');
            this.getData();
          } else {
            this.$Message.error(res.data.msg);
          }
        }).catch(erro => {
          this.$Message.error('服务器异常');
        });
      }
    },
  }
</script>
<style scoped>
  .select-wrapper {
    display: inline-block;
    margin-right: 5px;
  }
</style>



